<template>
    <a-layout>
        <a-layout-header>
            <a-row>
                <a-col :span="1">
                    <div class="logo"></div>
                </a-col>
                <a-col :span="7">
                    <div class="customerInformation">
                        <p
                            style="
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                            "
                        >
                            客户:20200218-xxxxxx
                        </p>
                        <p
                            style="
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                            "
                            :title="
                                this.userinfo.preProjectId +
                                '-' +
                                this.userinfo.curProjectName
                            "
                        >
                            项目:20200218-xxxxxx
                        </p>
