                </a-col>
                <a-col :span="16">
                    <div class="userNmae">XXX , <a>退出登陆</a></div>
                </a-col>
            </a-row>
        </a-layout-header>
        <a-layout-content>
            <div class="content">
                <div class="left">
                    <div class="left_search">
                        <a-input-search
                            placeholder="搜索"
                            size="large"
                            enter-button
                            @search="onSearch"
                        />
                    </div>
                    <div class="recommend">
                        <div class="recommend_title">推荐</div>
                        <div class="recommend_content">
                            <div
                                class="recommend_item"
                                v-for="item in recommendList"
                                :key="item"
                            >
                                <a>{{ item }}</a>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="right">
                    <div class="system_List">
                        <div
                            v-for="item in systemList"
                            :key="item"
