                            <a @click="goSystem(item)">{{ item }}</a>
                        </div>
                    </div>
                </div>
            </div>
        </a-layout-content>
        <a-layout-footer>
            2014-2021 苏ICP备 15042426号-5 版权所有 企查 查科技有限公司
            增值电信业务许可证: 苏ICP证B2-20180251 企业aaaa
        </a-layout-footer>
    </a-layout>
</template>

<script>
import sessionUtils from '@data/session/sessionUtils.js';
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
export default {
    name: 'App',
    data() {
        this.formatter = formatter;
        this.XEUtils = XEUtils;
        return {
            userinfo: {},
            recommendList: [
                '上季节交易汇总',
                '年度交易考核',
                '昨日内部交易内容',
            ],
            systemList: ['客户中心', '财务中心', '项目中心', '考试中心'],
        };
    },
    methods: {
        onSearch() {},
        goSystem(systemName) {
            this.$router.push('index_2')
        },
    },
};
</script>

<style lang="scss" scoped>
.userNmae {
    text-align: right;
    line-height: 60px;
    font-size: 18px;
    padding-right: 20px;
}
.ant-layout-content {
    overflow: auto;
    .content {
        width: 100%;
        height: 100%;
        .left {
            padding: 50px;
            float: left;
            width: 80%;
            height: 100%;
            .left_search {
                margin: 0 auto;
                width: 80%;
            }
            .recommend {
                margin-top: 60px;
                width: 100%;
                padding: 0 0 30px 200px;
                .recommend_title {
                    font-size: 20px;
                    font-weight: 700;
                }
                .recommend_content {
                    margin-top: 20px;
                    padding-left: 20px;
                    .recommend_item {
                        line-height: 30px;
                    }
                }
            }
        }
        .right {
            float: right;
            width: 20%;
            padding: 50px;
            padding-left: 0;
            height: 100%;
            .system_List {
                padding-left: 50px;
                border-left: 10px dotted #ccc;
                .system_item {
                    line-height: 30px;
                }
            }
        }
    }
}
.ant-layout-footer {
    width: 100%;
    -webkit-box-shadow: 0 0 5px 2px #dcdfe6;
    box-shadow: 0 0 5px 2px #dcdfe6;
    height: auto !important;
    min-height: 60px !important;
    z-index: 999;
    background: #fff !important;
    text-align: center;
    a {
        color: rgba(0, 0, 0, 0.65);
    }
}
</style>



